"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { DashboardHeader } from "@/components/dashboard/dashboard-header"
import { DashboardSidebar } from "@/components/dashboard/dashboard-sidebar"
import { DashboardContent } from "@/components/dashboard/dashboard-content"

// –¢–∏–ø –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è, —Å–æ–≤–º–µ—Å—Ç–∏–º—ã–π —Å DashboardContent
type User = {
  id: string
  email: string
  role: string
  name?: string | null
  profile?: {
    fullName?: string | null
  } | null
}

export default function DashboardPage() {
  const router = useRouter()
  const [user, setUser] = useState<User | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    const fetchUser = async () => {
      try {
        const userRes = await fetch("/api/user", { cache: "no-store" })
        if (!userRes.ok) {
          router.replace("/login")
          return
        }

        const userData = await userRes.json()
        const profileRes = await fetch("/api/profile", { cache: "no-store" })
        const profileData = profileRes.ok ? await profileRes.json() : null

        const composedUser: User = {
          id: userData.id,
          email: userData.email,
          role: userData.role,
          name: userData.name ?? null,
          profile: profileData ?? null,
        }

        setUser(composedUser)
      } catch (e) {
        setError("–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –ø—Ä–æ—Ñ–∏–ª—è. –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â—ë —Ä–∞–∑.")
        setTimeout(() => {
          router.replace("/login")
        }, 1500)
      } finally {
        setLoading(false)
      }
    }
    fetchUser()
  }, [router])

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center gradient-purple">
        <span className="text-lg text-white animate-pulse">–ó–∞–≥—Ä—É–∑–∫–∞...</span>
      </div>
    )
  }

  if (error) {
    return (
      <div className="min-h-screen flex flex-col items-center justify-center gradient-purple">
        <span className="text-lg text-red-200">{error}</span>
      </div>
    )
  }

  // –ï—Å–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –≤—Å—ë –∂–µ null, –Ω–µ —Ä–µ–Ω–¥–µ—Ä–∏–º –∫–æ–Ω—Ç–µ–Ω—Ç
  if (!user) {
    return null
  }

  return (
    <div className="min-h-screen gradient-purple">
      <DashboardHeader />
      <div className="flex">
        <DashboardSidebar />
        <DashboardContent user={user} />
      </div>
    </div>
  )
}
